h4 {
  margin: 0;
}
.participate {
  margin: 0;
  padding: 0 !important;
}
.participate > ol {
  list-style: none;
  margin: 0;
  padding-left: 0;
}
.participate > ol > li {
  padding-bottom: 5px;
}
.participate > ol > li > h4 {
  padding: 5px 0;
  font-size: 18px;
  color: white;
}
.participate > ol > li:first-child {
  background-color: #f8e2e2;
  background-image: -webkit-linear-gradient(135deg, white 25%, #f8e2e2 25%, #f8e2e2 50%, white 50%, white 75%, #f8e2e2 75%, #f8e2e2);
  background-image: -o-linear-gradient(135deg, white 25%, #f8e2e2 25%, #f8e2e2 50%, white 50%, white 75%, #f8e2e2 75%, #f8e2e2);
  background-image: linear-gradient(-45deg, white 25%, #f8e2e2 25%, #f8e2e2 50%, white 50%, white 75%, #f8e2e2 75%, #f8e2e2);
  background-size: 3px 3px;
}
.participate > ol > li:first-child > h4 {
  background-color: #ac3939;
}
.participate > ol > li:last-child {
  background-color: #e2e6f8;
  background-image: -webkit-linear-gradient(135deg, white 25%, #e2e6f8 25%, #e2e6f8 50%, white 50%, white 75%, #e2e6f8 75%, #e2e6f8);
  background-image: -o-linear-gradient(135deg, white 25%, #e2e6f8 25%, #e2e6f8 50%, white 50%, white 75%, #e2e6f8 75%, #e2e6f8);
  background-image: linear-gradient(-45deg, white 25%, #e2e6f8 25%, #e2e6f8 50%, white 50%, white 75%, #e2e6f8 75%, #e2e6f8);
  background-size: 3px 3px;
}
.participate > ol > li:last-child > h4 {
  background-color: #394dac;
}
.list-with-icon {
  list-style: none;
  margin: 10px 0;
  padding-left: 0;
}
.list-with-icon > li {
  margin: 15px 0;
}
.list-with-icon > li > a {
  display: inline-block;
  height: 70px;
  line-height: 70px;
  color: black;
  text-decoration: none;
  border: 1px solid #e6e6e6;
  padding: 0 12px;
  position: relative;
  -webkit-transition: all .2s;
  -o-transition: all .2s;
  transition: all .2s;
  background-color: white;
}
.list-with-icon > li > a::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(41, 163, 122, 0);
  -webkit-transition: all .2s;
  -o-transition: all .2s;
  transition: all .2s;
}
.list-with-icon > li > a:hover {
  border-color: #c2f0e0;
}
.list-with-icon > li > a:hover::after {
  background-color: rgba(41, 163, 122, 0.05);
}
.list-with-icon > li > a > img {
  width: 66px;
  height: 66px;
  margin-right: 10px;
  float: left;
}



