h3 {
  padding: 5px 0;
  margin: 0;
  font-weight: normal;
  font-size: 1.3em;
  position: relative;
  display: inline-block;
  padding: 5px 10px;
}
h3::before,
h3::after {
  content: '';
  height: 1px;
  width: 12vw;
  position: absolute;
  top: 50%;
  background-color: #33cc99;
}
h3::before {
  left: -12vw;
  background: -webkit-gradient(linear, left top, right top, from(white), to(#33cc99));
  background: -webkit-linear-gradient(left, white, #33cc99);
  background: -o-linear-gradient(left, white, #33cc99);
  background: linear-gradient(90deg, white, #33cc99);
}
h3::after {
  left: 100%;
  background: -webkit-gradient(linear, left top, right top, from(#33cc99), to(white));
  background: -webkit-linear-gradient(left, #33cc99, white);
  background: -o-linear-gradient(left, #33cc99, white);
  background: linear-gradient(90deg, #33cc99, white);
}
p {
  padding: 0 5px;
}
.ContestInfo > div {
  text-align: center;
  padding-bottom: 40px;
}
.ContestInfo > .date {
  padding-bottom: 10px;
}
.UpcomingContestInfo h3 {
  margin-bottom: 20px;
}
.links > ul {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  list-style: none;
  padding-left: 0;
  margin: 0;
}
.links > ul > li {
  width: 200px;
  height: 60px;
  -webkit-transition: all .2s;
  -o-transition: all .2s;
  transition: all .2s;
}
.links > ul > li > a {
