  transition: all .2s;
  margin: 15px;
  border: 1px solid #cccccc;
  background-color: white;
  -webkit-box-shadow: 3px 3px 12px -8px #000000;
          box-shadow: 3px 3px 12px -8px #000000;
}
.editorials > ul > li > a {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
