.Menu {
  list-style: none;
  padding-left: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  overflow-x: auto;
  border-bottom: 1px solid #e6e6e6;
}
.Menu > li > a {
  display: block;
  color: #4d4d4d;
  text-decoration: none;
  font-size: 18px;
  padding: 2px 10px;
}
.Menu > li.selected,
.Menu > li:hover {
  border-bottom: 2px solid #b82e2e;
}
.Menu > li.selected > a,
.Menu > li:hover > a {
  color: #333333;
}
.Menu > li + li {
  border-left: 1px dotted #e6e6e6;
}



